exports.push([module.id, "\n.register{\n    width: 100%;\n    height: 100%;\n}\n\n", "", {"version":3,"sources":["c:/Users/asd/Desktop/cd1706/vue1706/my/scripts/components/my/scripts/components/register.vue?310f14db"],"names":[],"mappings":";AAyEA;IACA,YAAA;IACA,aAAA;CACA","file":"register.vue","sourcesContent":["<template>\r\n  <div class=\"register\">\r\n      <h1><i class=\"iconfont icon-arrows-1-7\" @click=\"goback\"></i>注册</h1>\r\n      <div class=\"main\">\r\n          <div>+86<input type=\"text\" placeholder=\"请输入你的手机号码\" v-model=\"phonm\"><span @click=\"cheknum\">获取验证码</span></div>\r\n          <div><input type=\"text\" placeholder=\"请输入密码\" v-model=\"password\"></div>\r\n          <p>老用户请用原有的账号登录后进行手机绑定，直接用手机号注册将无法找回原有的优币和杂志，去帮助中心查看详情</p>\r\n           <div class=\"btn\"><button @click=\"next\">下一步</button></div> \r\n      </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport axios from \"axios\"\r\nimport { Toast } from 'mint-ui';\r\nimport router from \"../views/router\"\r\n\r\nexport default {\r\n    data(){\r\n        return{\r\n            phonm:\"\",\r\n            state:false,\r\n            password:\"\"\r\n        }\r\n    },\r\n    methods:{\r\n        cheknum(){\r\n            var reg=/^(13)|(15)|(18)\\d{9}$/\r\n            if(reg.test(this.phonm)==false){\r\n                 Toast({\r\n                    message: '格式输入错误',\r\n                    position: 'bottom',\r\n                    duration: 5000\r\n                    });\r\n            }else{\r\n                axios.get(\"/find\",{params:{id:this.phonm}}).then(res=>{\r\n                    console.log(res.data)\r\n                    if(res.data==0){\r\n                        console.log(\"ok\")\r\n                        this.state=true;      \r\n                    }else{\r\n                        Toast({\r\n                        message: '该用户已注册',\r\n                        position: 'bottom',\r\n                        duration: 5000\r\n                        })\r\n                    }\r\n                })\r\n            }\r\n        },\r\n        next(){\r\n            if(this.state==true){\r\n                console.log(this.password,this.phonm);\r\n                axios.post(\"/register\",{username:this.phonm,passwrod:this.password}).then(res=>{\r\n                   if(res.data==1){\r\n                    Toast({\r\n                    message: '注册成功',\r\n                    position: 'bottom',\r\n                    duration: 5000\r\n                    })\r\n                    router.push(\"/index/login\");   \r\n                    }\r\n                })\r\n            }\r\n        },\r\n        goback(){\r\n            router.go(-1)\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style>\r\n    .register{\r\n        width: 100%;\r\n        height: 100%;   \r\n    }\r\n  \r\n</style>\r\n"],"sourceRoot":""}]);

